@use "." as this;
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/AmountFormatter";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/DateTime";

$card-width: 20.75rem;

#{this.$selector} {
    min-width: $card-width;
    @include Display.set($display: flex);

    #{Button.$selector}.cardButton {
        @include Padding.set($preset: none);
        @include Color.set($bg: "transparent");
        @include Border.set($style: default);
        width: Layout.get-width(kappa);
        overflow: hidden;
        cursor: pointer;

        #{Text.$selector} {
            @include Color.set($fg: "black");
        }

        header {
            @include Flex.set($align: center);

            .avatar {
                @include Flex.set($shrink: none);
            }

            .operationInfo {
                overflow: hidden;

                &.operatioSpace {
                    @include Margin.set($left: alpha);
                }

                #{Text.$selector}.operationTag {
                    @include Font.set(
                        $size: delta,
                        $family: heading,
                        $weight: bold,
                        $space: nowrap
                    );
                    text-overflow: ellipsis;
                    overflow: hidden;
                }

                #{Text.$selector}.title {
                    @include Font.set($size: gamma, $space: nowrap);
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
            }
        }

        .amount {
            @include Margin.set($top: beta, $bottom: beta);

            #{AmountFormatter.$selector} {
                @include Font.set($align: left);

                > span,
                > data {
                    @include Font.set($size: delta, $family: heading, $weight: bold);
                    @include Color.set($fg: "black");
                }
            }
        }

        footer {
            #{DateTime.$selector} {
                @include Font.set($align: left);
                @include Color.set($fg: black);
                @include Display.set($display: block);
            }
        }
    }

    #{Button.$selector}.dropdownButton {
        width: var(--button-medium-size);
        height: var(--button-medium-size);
        cursor: pointer;

        @include Padding.set($preset: none);
        @include Color.set($bg: "transparent");
        @include Border.set($style: default);
        @include Flex.set($align: center, $justify: center);
    }

    #{Button.$selector}.dropdownItem {
        @include Color.set($bg: "transparent");
        @include Border.set($style: default);
        @include Font.set($align: left);

        span {
            @include Color.set($fg: black);
        }
    }
}
